.logo-customization-section {
  .p-field {
    margin-bottom: 1rem;
  }

  .action {
    cursor: pointer;
    transition: color 0.2s ease;

    &:hover {
      color: var(--primary-color);
    }

    &:disabled {
      cursor: not-allowed;
      opacity: 0.5;
    }
  }

  .max-w-150 {
    max-width: 150px;
  }

  .info-panel {
    background-color: #eff6ff;
    border: 1px solid #bfdbfe;
    border-radius: 0.5rem;
    padding: 1rem;
    margin-bottom: 1rem;

    .info-icon {
      color: #2563eb;
    }

    .info-text {
      font-weight: 500;
      color: #1e40af;
    }
  }

  .pending-changes {
    color: #ea580c;
    font-size: 0.875rem;
    font-weight: 500;
  }

  .pending-indicator {
    color: #ea580c;
    margin-left: 0.5rem;
    font-size: 0.875rem;
  }
}
